import React from "react";
import RentCar from "./rentCar";
import RentMotor from "./rentMotor";
import "./rent.scss";
import ModalUser from "./modal/formUser";
import RentSection from "./rentSection";
class Rent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      arrUser: [],
      isOpenModalUser: false,
      step: 1,
    };
  }
  prevStep = () => {
    const { step } = this.props;
    this.setState({
      step: 1,
    });
  };
  nextStep = () => {
    const { step } = this.props;
    this.setState({
      step: 2,
    });
  };
  handleAddNewUser = () => {
    this.setState({
      isOpenModalUser: true,
    });
  };
  toggleUserModal = () => {
    this.setState({
      isOpenModalUser: !this.state.isOpenModalUser,
    });
  };
  render() {
    //re-render
    const { step } = this.state;
    switch (step) {
      case 1:
        return (
          <React.Fragment>
            <div className="banner-container">
              <ModalUser
                // truyen function cho modalUser
                isOpen={this.state.isOpenModalUser}
                toggleFromParent={this.toggleUserModal}
                createNewUser={this.createNewUser}
                //
              />
              <div className="row">
                <div className="col-6">
                  <RentCar
                    nextStep={this.nextStep}
                    handleAddNewUser={this.handleAddNewUser}
                  />
                </div>
                <div className="col-5 titlee">
                  <span className="title-big">
                    Dch v thu xe uy tn v chuyn nghip
                  </span>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Thng tin r rng, minh bch</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Nhn bo gi nhanh chng, chi tit</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Xe cht lng cao, a dng chng loi</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>H tr chu o, tn tnh 24/7</span>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <RentSection />
            </div>
          </React.Fragment>
        );

      case 2:
        return (
          <React.Fragment>
            <div className="banner-container">
              <ModalUser
                // truyen function cho modalUser
                isOpen={this.state.isOpenModalUser}
                toggleFromParent={this.toggleUserModal}
                createNewUser={this.createNewUser}
                //
              />
              <div className="row">
                <div className="col-6">
                  <RentMotor
                    prevStep={this.prevStep}
                    handleAddNewUser={this.handleAddNewUser}
                  />
                </div>
                <div className="col-5 titlee">
                  <span className="title-big">
                    Dch v thu xe uy tn v chuyn nghip
                  </span>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Thng tin r rng, minh bch</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Nhn bo gi nhanh chng, chi tit</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>Xe cht lng cao, a dng chng loi</span>
                  </div>
                  <div className="title-small">
                    <i class="fas fa-check"></i>
                    <span>H tr chu o, tn tnh 24/7</span>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <RentSection />
            </div>
          </React.Fragment>
        );
      default:
        return null;
    }
  }
}
export default Rent;
